


















import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

export enum TimelineElementPosition {
  LEFT = 1,
  RIGHT = 3,
}

export interface TimelineElement {
  id: number
  header: string
  subheader: string
  information: string
  position: TimelineElementPosition
}

@Component
export default class TimelineContainer extends Vue {
  @Prop() private timelineElements!: TimelineElement[]
  private timelineContainerClass: string[] = ['timeline-container']
  private timelineElementClass: string[] = ['timeline-element']
  private getGridRows(): string {
    return `grid-template-rows: repeat(${this.timelineElements.length}, 1fr);`
  }

  created() {}

  private timelineElementPosition(position: TimelineElementPosition, index: number): string {
    const column: number = position
    const row: number = index + 1
    const textAlign: string = TimelineElementPosition[position].toLowerCase()

    return `grid-column: ${column}; grid-row: ${row}; text-align: ${textAlign}`
  }

  private markerPosition(position: TimelineElementPosition): string {
    return position === TimelineElementPosition.LEFT ? 'timeline-element-marker-left' : 'timeline-element-marker-right'
  }
}
